<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="服务名称">
                <el-input style="width: 500px" v-model="form.name" placeholder="请输入"></el-input>
            </el-form-item>

            <el-form-item label="服务价格">
                <el-input style="width: 500px" v-model="form.price" placeholder="请输入"></el-input>
            </el-form-item>

            <el-form-item label="联系外链地址">
                <el-input style="width: 500px" v-model="form.address" placeholder="请输入"></el-input>
            </el-form-item>

            <!--<el-form-item label="发布时间">-->
            <!--<el-date-picker-->
            <!--v-model="form.publishtime"-->
            <!--:disabled="isNoEdit"-->
            <!--type="date"-->
            <!--placeholder="选择日期">-->
            <!--</el-date-picker>-->
            <!--</el-form-item>-->

            <el-form-item label="选择分类">
                <el-select
                        v-model="form.category_id" placeholder="请选择">
                    <el-option v-for="item in options"
                               :key="item.id"
                               :label="item.name"
                               :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="封面图">
                <image-select v-model="img" :url="form.picture"></image-select>
            </el-form-item>

            <el-form-item label="服务简介">
                <el-input
                        type="textarea"
                        style="width: 400px"
                        :rows="5"
                        v-model="form.description" placeholder="请输入">
                </el-input>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            label="启用"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不启用"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="推荐">
                <el-select v-model="form.recommend" placeholder="请选择">
                    <el-option
                            label="推荐"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不推荐"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>
            <div>
                <rich-view v-model="form.rich_text"></rich-view>
            </div>
            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>

    import api from '../../js/api/service'

    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    id: null,
                    name: null,
                    description: null,
                    // picture: null,
                    price: null,
                    recommend: null,
                    status: null,
                    address: null,
                    rich_text: null,
                    category_id: null,
                },
                img: null,
                isNoEdit: true,
                isLoading: false,
                options: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                if (this.$route.query.id !== undefined) {
                    this.isNoEdit = true;
                    this.loadData()
                } else {
                    this.isNoEdit = false;
                }
                this.loadCategory();
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                })
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            loadCategory() {
                api.spinner(res => {
                    this.options = res.data.data;
                })
            },
            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.name)) {
                    this.$message.error('请输入名称');
                    return;
                }

                if (!api.isStrValid(this.form.description)) {
                    this.$message.error('请输入简介');
                    return;
                }
                if (!api.isStrValid(this.form.price)) {
                    this.$message.error('请输入价格');
                    return;
                }

                if (!api.isStrValid(this.form.status)) {
                    this.$message.error('请选择状态');
                    return;
                }

                if (!api.isStrValid(this.form.address)) {
                    this.$message.error('请输入外链地址');
                    return;
                }
                if (!api.isStrValid(this.form.rich_text)) {
                    this.$message.error('请输入富文本详情');
                    return;
                }
                if (!api.isStrValid(this.form.category_id)) {
                    this.$message.error('请选择分类');
                    return;
                }
                if (this.img == null && this.form.picture == null) {
                    this.$message.error('请选择图片');
                    return;
                }
                let data = api.objToFormData(this.form);
                this.isLoading = true;
                data.append('picture', this.img);
                api.save(data, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'service'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            },
            back() {
                this.$router.back();
            },

        }
    }
</script>
